














































import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';
import { Getter, namespace } from 'vuex-class';
import { UserInfo } from '@/types';
import UserService from '@/services/userService';
const Auth = namespace('Auth');
@Component({
  components: {

  }
})
export default class TickerSelect extends Vue {
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;

  @Auth.Action
  private getUserInfo!: () => Promise<any>;

  private detailMsg = {
    type: '',
    msg: ''
  };

  listCoin = []
  searchText = ''
  loading = true
  listSelected: any[] = []
  async created () {
    this.loading = true;
    !this.userData && await this.getUserInfo();
    this.listSelected = this.userData.tickerList;
    this.listCoin = await axios
      .get('https://api.binance.com/api/v3/ticker/price')
      .then(response => {
        return response.data.filter((x: { symbol: string | string[] }) => x.symbol.indexOf('USDT') !== -1);
      });
    this.loading = false;
  }

  async onListSelectedChange (val: any) {
    if (!this.loading) {
      this.loading = true;
      if (this.listSelected.includes(val)) {
        this.listSelected.splice(this.listSelected.indexOf(val), 1);
      } else {
        this.listSelected.push(val);
      }
      await UserService.changeTickerList(this.listSelected).then(
        async () => {
          await this.getUserInfo();
        },
        (error) => {
          this.detailMsg = {
            type: 'error',
            msg: (error.response && error.response.data && error.response.data.msg)
          };
        }
      );
      this.loading = false;
    }
  }

  get filterList () {
    return !this.searchText ? this.listCoin : this.listCoin.filter((x: { symbol: string | string[] }) => x.symbol.indexOf(this.searchText.toUpperCase()) !== -1);
  }
}
